<template>
  <div id="note-editor">
    <textarea
      :value="activeNoteText"
      @input="editNote"
      class="form-control">
    </textarea>
  </div>
</template>

<script>
import { editNote } from '../../vuex/actions'

export default {
  vuex: {
    getters: {
      activeNoteText: (state) => {
          return this.$state.activeNote.text
      }
    },
    actions: {
      editNote
    }
  }
}
</script>